@import url(https://fonts.googleapis.com/css?family=Roboto:500|Roboto+Slab:400&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Roboto', Helvetica, sans-serif;
  font-weight: 500;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Roboto Slab', 'Roboto', Helvetica, sans-serif;
  font-weight: 400;
  font-style: normal;
}

h3, h4, h5, h6 {
  font-family: 'Roboto', Helvetica, sans-serif;
  font-style: italic;
}

html, body, #root {
	height: 100%;
}
/* InputField component styles */

.input-field {
	display: flex;
	flex-direction: column;
}

.slider-text {
	display: flex;
	width: 100%;
	align-items: baseline; 
	justify-content: space-between;
	margin: 0;
	padding: 0;
}

.input-field input[type=range] {
	width: 100%;
	height: 25px;
}

.value {
	font-size: 1.25rem;
	font-weight: 600;
}

.value .unit {
	font-size: 1rem;
	font-weight: normal;
}

select {
	font-size: 1rem;
	margin-left: 0.5rem;
	border: none;
	padding: .3em 2em .3em .5em;
	-webkit-appearance: none;
	appearance: none;
	border-radius: 0.4em;
	background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23000000%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
	  linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
	background-repeat: no-repeat, repeat;
	background-position: right .7em top 50%, 0 0;
	background-size: .65em auto, 100%;
}

.select-input {
	text-align: right;
	margin-bottom: 0.75rem;
}
.hub-circle, .blade-side-1 {
  fill: #e0e0e0;
}

.hub-center, .blade-side-2 {
  fill: #f0f0f0;
}

.hub-shine, .blade-side-2 {
  fill: #ffffff;
}

.tower {
	fill: #ffffff;

}

.tower-shadow {
	fill: #e0e0e0;
}
.tower {
	fill: #ffffff;

}

.tower-shadow {
	fill: #e0e0e0;
}
#sky {
  fill: url(#sky-gradient);
}

#mountain-far {
  fill: url(#mountain-far-gradient);
}

#mountain-near {
  fill: url(#mountain-near-gradient);
}

#hills {
  fill: url(#hills-gradient);
}

#sea {
  fill: url(#sea-gradient);
}
/* TurbineVisual component styles */

.turbine-visual, .turbine-visual .root-svg {
  position: fixed;
  box-sizing: border-box;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
}

.cls-1 {
  fill: url(#linear-gradient);
}

.tower {
  fill: url(#linear-gradient-6);
}

.axil {
  fill: url(#radial-gradient-2);
}

.windstream {
  opacity: 0.5;
}

.cls-10 {
  fill: url(#linear-gradient-7);
}

.cls-11 {
  fill: url(#linear-gradient-8);
}

.cls-12 {
  fill: url(#linear-gradient-9);
}

.cls-13 {
  fill: url(#linear-gradient-10);
}

.cls-14 {
  fill: url(#linear-gradient-11);
}

.cls-15 {
  fill: url(#linear-gradient-12);
}

.cls-16 {
  fill: url(#linear-gradient-13);
}
/* TurbineCalculator component styles */

.turbine-calculator {
	display: flex;
	flex-direction: column;
}

.results {
	text-align: right;
	margin-bottom: 1rem;
}

.results .unit {
	font-size: 1.5rem;
	font-weight: normal;
}

.results h2, .results h3 {
	font-weight: normal;
	margin: 0;
}

.results h2 {
	font-size: 12vh;
	margin: 0;
}

.results h3 {
	line-height: 1.5rem;
	margin-top: 0;
}

.inputs {
	padding: 1.5rem;
	background-color: rgba(255, 255, 255, 0.35);
	border-radius: 10px;
}

@media (orientation: landscape) and (min-width: 896px) {
	.turbine-calculator {
		align-items: flex-end;
		flex-direction: column-reverse;
		padding-right: 2rem;
	}

	.inputs {
		min-width: 400px;
		margin-top: 1rem;
	}

	.results {
		margin-top: 3rem;
	}
}
/* App component styles */

.app {
	position: relative;
	box-sizing: border-box;
	height: 100%;
	padding: 1rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.app h1 {
	margin: 0;
	font-size: 6vmin;
	font-weight: normal;
}

@media (orientation: landscape) and (min-width: 896px) {
	.app {
		justify-content: flex-start;
	}
}
